import * as React from 'react';
import { RenderToolbar, ItemKeys, IBasicButton } from '@ali/4ever-component';
import createDecreaseIndentButton, { IDecreaseIndentButtonProps } from './components/next/createDecreaseIndentButton';
import createIncreaseIndentButton, { IIncreaseIndentButtonProps } from './components/next/createIncreaseIndentButton';


const createButtons: Partial<Record<ItemKeys, (props: IDecreaseIndentButtonProps | IIncreaseIndentButtonProps) => IBasicButton>> = {
  'indent.decrease': createDecreaseIndentButton,
  'indent.increase': createIncreaseIndentButton
};

export default function createRenderToolbar(
  locale: Record<string, string>,
): RenderToolbar {

  return (props, controller) => {

    const { buttonName, extraProps } = props;

    const createButton = createButtons[buttonName]

    if (createButton) {
      return createButton({
        controller,
        locale,
        ...extraProps,
      })
    }

    return null
  };
}
